.filter_date__start {
    background-color:#2c2c2c;
    color: white;
    padding: 9px;
    border-radius: 10px;
    border: none;
    text-transform: uppercase;
    font-weight: bold;
    cursor: pointer;
    margin-right: 20px;
    transition-duration: 0.5s;
}

.filter_date__end {
    background-color: white;
    color: #2c2c2c;
    padding: 7px;
    border-radius: 10px;
    text-transform: uppercase;
    font-weight: bold;
    cursor: pointer;
    margin-right: 20px;
    transition-duration: 0.5s;
}

.filter_date__start:hover {
    color: rgb(0 173 255);
}

.filter_date__end:hover {
    color: rgb(0 173 255);
    background-color: #2c2c2c;
}

::-webkit-calendar-picker-indicator {
    background-color: white;
    border-radius: 5px;
}   

.filter_btn {
    background-color: white;
    color: #2c2c2c;
    width: 100px;
    padding: 9px;
    border-radius: 10px;
    text-transform: uppercase;
    font-weight: bold;
    cursor: pointer;
    transition-duration: 0.5s;
}

.filter_btn:hover {
    color: rgb(0 173 255);
    background-color: #2c2c2c;
}

@media screen and (max-width: 640px) {
    .filter_date__start {
        padding: 7px;  
        margin: 0px;
        width: 45%;      
    }
    .filter_date__end {
        padding: 5px;  
        margin: 0px;
        width: 45%;      
    }
    .filter_btn {
        padding: 7px;
        margin-top: 10px;
        width: 98%;
    }
}